import React, {Component} from "react";

//IMPORT ICON
import Sad from './Resources/sad.png';
import Funny from './Resources/funny.png';
import Angry from './Resources/angry.png';
import Love from './Resources/love.png';
import Like from './Resources/like.png';

class ReactionBox extends Component {
    render() {
        return(
            <div className="reaction_box_container">
                <div className="reaction_container">
                    <div className="react_container">
                        <div className="react" style={{background: `url(${Sad}) no-repeat center center / cover`}}></div>
                    </div>
                    <div className="react_container">
                        <div className="react" style={{background: `url(${Angry}) no-repeat center center / cover`}}></div>
                    </div>
                    <div className="react_container">
                        <div className="react" style={{background: `url(${Love}) no-repeat center center / cover`}}></div>
                    </div>
                    <div className="react_container">
                        <div className="react" style={{background: `url(${Funny}) no-repeat center center / cover`}}></div>
                    </div>
                    <div className="react_container">
                        <div className="react" style={{background: `url(${Like}) no-repeat center center / cover`}}></div>
                    </div>
                </div>
            </div>
        )
    }
}

export {
    ReactionBox
}